/* eslint-disable no-unused-vars */
import logo from "./logo.svg";
import "./App.css";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Suspense, createContext, lazy, useEffect, useState } from "react";
import data from "./data.js";
import {
  Link,
  Route,
  Routes,
  Outlet,
  useNavigate,
  json,
} from "react-router-dom";
import Card from "./components/card.js";
import MainPage from "./routes/MainPage.js";
import axios from "axios";
import { useQuery } from "react-query";
// import Detail from "./routes/Detail.js";
// import Cart from "./routes/Cart.js";
// lazy import
const Detail = lazy(() => import("./routes/Detail.js"));
const Cart = lazy(() => import("./routes/Cart.js"));

// context   .(context state   .)
export let Context1 = createContext();

function App() {
  useEffect(() => {
    if (localStorage.getItem("watchedItem") === true) {
      return null;
    } else {
      const watchedItem = [];
      localStorage.setItem("watchedItem", JSON.stringify(watchedItem));
    }
  }, []);

  let [shoes, setShoes] = useState(data);

  let [stock, setStock] = useState([10, 11, 12]);

  // useNavigate() ->   .
  let navigate = useNavigate();

  // React Query  ajax .
  let result = useQuery(
    [""],
    () => {
      return axios
        .get("https://codingapple1.github.io/userdata.json")
        .then((a) => {
          console.log("How many times does it refetch");
          return a.data;
        });
    },
    // 2000ms  Refetch  
    { staleTime: 2000 }
  );

  // result.data -> ajax      .
  // result.isLoading -> ajax  true 
  // result.error -> ajax    true 

  return (
    <div className="App">
      <Navbar bg="light" data-bs-theme="light">
        <Container>
          <Navbar.Brand href="#home">ShoesShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link
              onClick={() => {
                navigate("/");
              }}
            >
              Home
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                navigate("/cart");
              }}
            >
              Cart
            </Nav.Link>
          </Nav>
        </Container>
        <Nav className="me-auto">
          {result.isLoading ? "" : result.data.name}
        </Nav>
      </Navbar>
      <Suspense fallback={<div></div>}>
        <Routes>
          <Route
            path="/"
            element={<MainPage shoes={shoes} setShoes={setShoes}></MainPage>}
          />
          <Route
            path="/detail/:id"
            element={
              <Context1.Provider value={{ stock, shoes }}>
                <Detail shoes={shoes} setShoes={setShoes}></Detail>
              </Context1.Provider>
            }
          />

          {/*   */}
          <Route path="/cart" element={<Cart></Cart>} />

          <Route path="/about" element={<About></About>}>
            <Route path="member" element={<div></div>} />
            <Route path="location" element={<div></div>} />
          </Route>
          <Route path="/event" element={<Event></Event>}>
            <Route path="one" element={<p>    </p>} />
            <Route path="two" element={<p>   </p>} />
          </Route>

          <Route path="*" element={<div> </div>} />
        </Routes>
      </Suspense>
    </div>
  );
}

function About() {
  return (
    <div>
      <h1></h1>
      <Outlet></Outlet>
    </div>
  );
}

function Event() {
  return (
    <div>
      <h4> </h4>
      <Outlet></Outlet>
    </div>
  );
}

export default App;
